.main{
    height: 1080px;
    width: 100%;
    background: url("../../../assets/img/bg/main-bg.jpg") center center/cover no-repeat;
    padding-top: 171px;
    &__title{
        font-size: 72px;
        line-height: 84px;
        font-weight: 400;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: capitalize;
        color: #FFFFFF;
        font-family: "Helvetica-Roman";
        & span{
            font-weight: bold;
        }
    }
    &__subtitle{
        width: 595px;
        font-family: "Helvetica-Roman";
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        text-align: center;
        letter-spacing: -0.2px;
        color: rgba($color: white, $alpha: 0.7);
        margin: 0 auto;
        margin-top: 16px;
    }
    &__btn{
        display: block;
        width: 220px;
        height: 72px;
        background: linear-gradient(#faff1d, #907853de);
        border-radius: 43px;
        margin: 0 auto;
        margin-top: 40px;
        outline: none;
        border: none;
        cursor: pointer;
        font-family: "Helvetica-Roman";
        font-weight: 700;
        font-size: 18px;
        letter-spacing: 0.2em;
        color: #1A1A1A;
        transition: 0.5s all cubic-bezier(.5,1,.5,1);
        &:hover{
            transform: scale(1.1,1.1);
        }
    }
    &__data{
        width: 1110px;
        height: 95px;
        margin-top: 70px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        &__item{
            width: 245px;
            height: 95px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-family: "Helvetica-Roman";
            margin-right: 90px;
            &:last-child{
                margin-right: 0;
            }
            & .item__img{
                margin-top: 5px;
            }
            & .item__text_one{
                font-size: 16px;
                text-align: center;
                color: #FFFFFF;
                margin-top: 16px;
            }
            & .item__text_two{
                font-size: 14px;
                text-align: center;
                color: rgba($color: white, $alpha: 0.5);
                margin-top: 10px;
            }
        }
    }
}
.container{
    width: 1110px;
    margin: 0 auto;
}
@media (max-width: 1178px) {
    .main{
        &__title{
            width: 1000px;
            display: block;
            font-size: 68px;
            margin: 0 auto;
        }
        &__data{
            width: 1000px;
            height: 95px;
            &__item{
                width: 245px;
                height: 95px;
            }
        }
    }
    .container{
        width: 1000px;
    }
}
@media (max-width: 998px) {
    .main{
        &__title{
            width: 900px;
            display: block;
            font-size: 64px;
            margin: 0 auto;
        }
        &__data{
            width: 900px;
            height: 95px;
            &__item{
                width: 245px;
                height: 95px;
            }
        }
    }
    .container{
        width: 900px;
    }
}
@media (max-width: 768px) {
    .main{
        &__title{
            width: 600px;
            display: block;
            font-size: 48px;
            margin: 0 auto;
        }
        &__data{
            width: 600px;
            height: 95px;
            &__item{
                width: 245px;
                height: 95px;
            }
        }
    }
    .container{
        width: 600px;
    }
}
@media (max-width: 586px) {
    .main{
        height: 960px;
        &__title{
            width: 450px;
            display: block;
            font-size: 36px;
            margin: 0 auto;
        }
        &__subtitle{
            display: none;
        }
        &__btn{
            display: block;
            width: 180px;
            height: 50px;
            background: linear-gradient(#faff1d, #907853de);
            margin-top: 40px;
            font-weight: 700;
            font-size: 18px;
        }
        &__data{
            width: 100%;
            height: 95px;
            padding: 0 20px;
            &__item{
                width: 245px;
                height: 95px;
                margin-right: 0;
                & .item__text_one{
                    font-size: 12px;
                    margin-top: 16px;
                }
                & .item__text_two{
                    font-size: 12px;
                    margin-top: 10px;
                }
            }
        }
    }
    .container{
        width: 100%;
    }
}