footer{
    width: 100%;
    height: 275px;
    background: #252525;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer__data{
    width: 100%;
    height: 155px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid #303030;
    margin: 0 auto;
    &__item{
        width: 245px;
        height: 95px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-family: "Helvetica-Roman";
        margin-right: 90px;
        &:last-child{
            margin-right: 0;
        }
        & .item__img{
            margin-top: 5px;
        }
        & .item__text_one{
            font-size: 16px;
            text-align: center;
            color: #FFFFFF;
            margin-top: 16px;
        }
        & .item__text_two{
            font-size: 14px;
            text-align: center;
            color: rgba($color: white, $alpha: 0.5);
            margin-top: 10px;
        }
    }
}
.footer__logo{
    height: 24px;
    width: 222px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    margin-top: 45px;
}
@media (max-width: 768px) {
    .footer__data{
        padding: 0 20px;   
        display: flex;
        justify-content: center;
        align-items: center;
        &__item{
            width: 200px;
            height: 95px;
            display: flex;
            margin-right: 90px;
            & .item__text_one{
                font-size: 12px;
                margin-top: 16px;
            }
        }
    }
}
@media (max-width: 586px) {
    .footer__data{
        padding: 0 20px;   
        display: flex;
        justify-content: center;
        align-items: center;
        &__item{
            width: 33%;
            height: 95px;
            display: flex;
            margin-right: 0;
            & .item__text_one{
                font-size: 12px;
                margin-top: 16px;
            }
        }
    }
}