.about{
    width: 1110px;
    height: 740px;
    padding-top: 60px;
    position: absolute;
    z-index: 10;
    top: 800px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(37, 37, 37, 1);
    border-radius: 4px;
    &__title{
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 351px;
        height: 150px;
        font-family: 'Helvetica-Black';
        font-weight: 900;
        font-size: 100px;
        line-height: 150px;
        text-transform: uppercase;
        color: #303030;
        margin: 0 auto;
    }
    &__subtitle{
        margin: 0;
        padding: 0;
        position: absolute;
        top: 105px;
        left: 50%;
        transform: translateX(-50%);
        width: 526px;
        height: 60px;
        font-family: 'Helvetica-Roman';
        font-weight: 400;
        font-size: 30px;
        line-height: 60px;
        text-align: center;
        text-transform: capitalize;
        letter-spacing: 0.2em;
        color: #D9D9D9;
    }
    &__descr{
        width: 734px;
        height: 60px;
        font-family: 'Helvetica-Roman';
        font-weight: 400;
        font-size: 16px;
        line-height: 30px;
        text-align: center;
        color: #A6A6A6;
        margin: 0 auto;
        margin-top: 31px;
    }
}
@media (max-width: 1178px) {
    .about{
        width: 900px;
    }
}
@media (max-width: 998px) {
    .about{
        width: 800px;
    }
}
@media (max-width: 768px) {
    .about{
        width: 550px;
        height: 690px;
        &__descr{
            width: 500px;
            height: 60px;
            font-family: 'Helvetica-Roman';
            font-weight: 400;
            font-size: 16px;
            line-height: 30px;
            margin-top: 20px;
        }
    }
}
@media (max-width: 586px) {
    .about{
        width: 100%;
        height: 690px;
        top: 700px;
        padding-top: 30px;
        &__subtitle{
            top: 70px;
            left: 50%;
            width: 100%;
            height: 60px;
        }
        &__descr{
            width: 450px;
            height: 60px;
            font-family: 'Helvetica-Roman';
            font-weight: 400;
            font-size: 16px;
            line-height: 30px;
            margin-top: 20px;
        }
    }
}