.cart{
    width: 100%;
    min-height: calc(100vh - 90px - 275px);
    background: url('../../../assets/img/bg/cart-bg.jpg') center center/cover no-repeat;
    padding-top: 120px;
    &__wrap{
        width: 1110px;
        min-height: calc(100vh - 90px - 275px);
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        &__item{
            width: 100%;
            height: 100px;
            display: flex;
            align-items: center;
            background-color: rgba(26, 26, 26, 1);
            padding: 0 40px;
            border-radius: 8px;
            position: relative;
            margin-bottom: 10px;
            &__img{
                width: 120px;
                height: 80px;
                & img{
                    width: 100%;
                    height: 100%;
                }
            }
            &__title{
                font-family: 'Helvetica-Roman';
                font-weight: 400;
                font-size: 32px;
                color: #D9D9D9;
                text-shadow: 2px 2px 4px #907853de;
                text-align: center;
                margin-left: 80px;
            }
            &__quant{
                font-family: 'Helvetica-Roman';
                font-weight: 400;
                font-size: 32px;
                color: #D9D9D9;
                text-shadow: 2px 2px 4px #907853de;
                position: absolute;
                right: 80px;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}
@media (max-width: 1100px) {
    .cart{
        width: 100%;
        min-height: calc(100vh - 90px - 275px);
        &__wrap{
            width: 900px;
            min-height: calc(100vh - 90px - 275px);
            &__item{
                width: 100%;
                height: 100px;
                &__img{
                    width: 120px;
                    height: 80px;
                }
            }
        }
    }
}
@media (max-width: 990px) {
    .cart{
        width: 100%;
        min-height: calc(100vh - 90px - 275px);
        &__wrap{
            width: 800px;
            min-height: calc(100vh - 90px - 275px);
            &__item{
                width: 100%;
                height: 100px;
                &__img{
                    width: 120px;
                    height: 80px;
                }
            }
        }
    }
}
@media (max-width: 768px) {
    .cart{
        width: 100%;
        &__wrap{
            width: 600px;
            &__item{
                width: 100%;
                height: 80px;
                &__img{
                    width: 120px;
                    height: 60px;
                }
                &__title{
                    font-weight: 400;
                    font-size: 26px;
                }
                &__quant{
                    font-weight: 400;
                    font-size: 26px;
                }
            }
        }
    }
}
@media (max-width: 568px) {
    .cart{
        width: 100%;
        &__wrap{
            width: 100%;
            padding: 0 20px;
            &__item{
                width: 100%;
                height: 80px;
                &__img{
                    width: 100px;
                    height: 60px;
                }
                &__title{
                    font-weight: 400;
                    font-size: 22px;
                    margin-left: 40px;
                }
                &__quant{
                    font-weight: 400;
                    font-size: 22px;
                }
            }
        }
    }
}
