.about__slider{
    width: 735px;
    height: 270px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: rgba(37, 37, 37, 1);
    margin: 0 auto;
    margin-top: 90px;
    &__prev{
        width: 60px;
        height: 60px;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        cursor: pointer;
        & img{
            width: 100%;
            height: 100%;
            transition: 0.5s all;
            &:hover{
                transform: scale(1.1, 1.1);
            }
        }
    }
    &__next{
        width: 60px;
        height: 60px;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        transition: 0.5s all;
        cursor: pointer;
        & img{
            width: 100%;
            height: 100%;
            transition: 0.5s all;
            &:hover{
                transform: scale(1.1, 1.1);
            }
        }
    }
    &__wrap{
        width: 580px;
        height: 270px;
        display: flex;
        justify-content: center;
        align-items: center;
        &__item{
            width: 580px;
            height: 270px;
            &__hide{
                display: none;
            }
            &__animate-slide{
                animation: animate 1s ease-in;
                animation-fill-mode: both;
                display: flex;
            }  
            & img{
                width: 100%;
                height: 100%;
                border-radius: 8px;          
            }
        }
    }
}
@keyframes animate{
    from{
        opacity: 0;
        transform: translateY(50%);
    }50%{
        transform: translateY(-10%);
        opacity: 0.5;
    }to{
        opacity: 1;
    }

}
@media (max-width: 768px) {
    .about__slider{
        width: 500px;
        height: 270px;
        margin-top: 70px;
        &__prev{
            width: 30px;
            height: 30px;
        }
        &__next{
            width: 30px;
            height: 30px;
        }
        &__wrap{
            width: 420px;
            height: 270px;
            &__item{
                width: 420px;
                height: 270px;
            }
        }
    }
}
